.header {
    .container {
        display: flex;
        flex-flow: row wrap;
    }
    .block {
        flex-basis: 50%;
        .inner {
            padding: rem(60) rem(75);
            .content {
                display: flex;
                flex-direction: column;
                margin-bottom: rem(135);
                .logo {
                    margin-bottom: 10px;
                }
                .title {
                    white-space: pre-line;
                    color: $light-blue;
                    &::first-line {
                        color: $blue;
                    }
                }
            }
            .media {
                display: flex;
                flex-flow: row wrap;
                justify-content: space-between;
                .soc-media {
                    display: flex;
                    flex-basis: 35%;
                    justify-content: space-between;
                    &__icon {
                        color: $light-blue;
                    }
                }
                .contact {
                    display: flex;
                    flex-basis: 30%;
                    justify-content: space-between;
                    .phone {
                        font-family: $font-lekton-regular;
                        color: $light-blue;
                    }
                }
            }
        }
    }
    .left-part {
        padding: rem(35) rem(25);

        background-color: $yellow;
    }
    .menu-burger {
        &__icon > .soc-media__icon {
            font-size: $quote;
            color: #4bc7c7;
            cursor: pointer;
        }
    }
    #menu-checkbox {
        display: none;
    }
}
